<template>
  <ValidationObserver ref="accountReceivableForm" v-slot="invalid">
    <section class="request account-receivable-confirm-create">
      <div class="request-header" >
        <div class="request-header-container">
          <div class="title">
            <h4></h4>
            <div class="number">
              <label class="application-num">: {{item.cd}}</label>
              <label class="payment-num">: {{item.settlement_number}} </label>
            </div>
          </div>
          <div class="request-header_status">
            <div v-html="requestStatus"></div>
          </div>
        </div>
      </div>
      <b-container fluid class="request-body customScrollBar">
        <!--  -->
        <collapse>
          <template v-slot:collapse-header></template>
          <template v-slot:collapse-body>
            <b-row class="px-4 py-1">
              <b-col md="4" lg="4" class="pl-0">
                <DateSskf 
                  v-model="item.targetOn" 
                  valueType="YYYY-MM-DD" 
                  id="targetYear"
                  name=""
                  :changed="invalid.changed"
                  :rules="{ required: true }"
                  :disabled-field="disabled"
                  formatDate="YYYY/MM"/>
              </b-col>
              <b-col md="6" lg="7">
                <InputSskf 
                  id="customer-name" 
                  name="" 
                  v-model="item.requestModel.torihikiNm"
                  :changed="invalid.changed"
                  :disabled-field="disabled" 
                  :rules="{ required: true }"/>
              </b-col>
            </b-row>
            <b-row class="px-4 py-1 mt-3">
              <b-col md="5" :lg="item.purpose == '' ? 7 : 4 " class="pl-0">
                <div class="purpose-container">
                  <SelectSskf 
                    name=""
                    id="purpose"
                    v-model="item.purpose"
                    :changed="invalid.changed"
                    :rules="{required: true}"  
                    :isNormalSelect="true"
                    :options="useTypesList" 
                    :disabled-field="disabled">
                  </SelectSskf>
                  <InputSskf
                    v-if="item.purpose == ''"
                    id="other-purpose"
                    class="other-purpose"
                    v-model="item.purposeOfOthers"
                    :changed="invalid.changed"
                    :disabled-field="disabled" 
                    :rules="{ required: true }"/>
                </div>
              </b-col>
              <b-col md="5" lg="5">
                <SelectSskf 
                  name="/"
                  id="inner_outner"
                  v-model="item.commissionType"
                  :changed="invalid.changed"
                  :rules="{required: true}"  
                  :isNormalSelect="true"
                  :options="commissionTypeList"
                  :disabled-field="disabled">
                </SelectSskf>
              </b-col>
            </b-row>
            <b-row class="px-4 py-1 mt-3">
              <b-col md="5" lg="4" class="pl-0">
                <InputNumbericSskf
                  separator="," 
                  name="()" 
                  id="request_billing_amount"
                  v-model="item.requestModel.billingAmount"
                  :min=1
                  :rules="{required: true, validNumber:[1,10]}"
                  :disabled-field="disabled"/>
              </b-col>
              <b-col md="5" lg="5">
                <DateSskf
                  id="billing_date"
                  v-model="item.billing_on"
                  name=""
                  valueType="YYYY-MM-DD"
                  :changed="invalid.changed"
                  :rules="{ required: true }"
                  :disabled="disabled"
                  formatDate="YYYY/MM"/>
              </b-col>
            </b-row>
            <b-row class="px-4 py-1 mt-3">
              <b-col md="5" lg="4" class="pl-0">
                <SelectSskf
                  v-if="item.requestModel.approvalFlowDetailsRequest"
                  name=""
                  id="department"
                  v-model="item.requestModel.approvalFlowDetailsRequest.bumonCd"
                  :changed="invalid.changed"
                  :rules="{required: true}"  
                  :isNormalSelect="false"
                  :options="departmentList"
                  :disabled-field="disabled">
                </SelectSskf>
              </b-col>
              <b-col md="5" lg="5">
                  <SelectSskf
                    v-if="item.requestModel.approvalFlowDetailsRequest"
                    name=""
                    id="department"
                    v-model="item.requestModel.approvalFlowDetailsRequest.shainCd"
                    :changed="invalid.changed"
                    :rules="{required: true}"  
                    :isNormalSelect="false"
                    :reduce="text => text.value"
                    :options="applicationList"
                    :disabled-field="disabled">
                  </SelectSskf>
              </b-col>
            </b-row>
            <b-row class="px-4 py-1 mt-3">
              <b-col md="12" lg="12" class="pl-0">
                <TextareaSskf name="" id="remarks" v-model="item.remarks" :row='3' :disabled-field="disabled"/>
              </b-col>
            </b-row>
            <!-- Files -->
            <b-row class="px-4 py-1 mt-3 attachFile" v-if="disabled" key="viewListFiles">
              <b-col md="3" lg="auto" class="attachFile_body px-2 border-0">
                <b-row>
                  <b-col cols="12" class="p-0">
                    <div class="label_input">
                      <label></label>
                    </div>
                  </b-col>
                </b-row>
              </b-col>
              <b-col md="12" lg="6" class="attachFile_filename">
                <b-row v-for="file in item.requestModel.fileModelList"
                  :key="file.requestAttachmentCd"
                  no-gutters
                >
                  <b-col md="auto" class="px-3">
                    <i class="fas fa-file"></i>
                    <a href="#">{{file.fileOriginalName}}</a>
                  </b-col>
                </b-row>
              </b-col>
            </b-row>
            <b-row class="px-4 py-1 attachFile mt-3" v-else key="uploadFiles">
              <UploadFile :item="item"/>
            </b-row>
          </template>
        </collapse>
        <!--  -->
        <collapse>
          <template v-slot:collapse-header></template>
          <template v-slot:collapse-body>
            <b-row class="px-4 py-1 mt-3">
              <b-col md="5" lg="4" class="pl-0">
                <b-form-group label="" label-for="payment_place" class="label_input">
                  <v-select
                    class="main-vselect"
                    v-model="item.requestModel.paymentPlace" 
                    :options="paymentPlaceList"
                    :reduce="bumonNm => bumonNm.bumonNm" label="bumonNm"
                    :clearable="false"
                    disabled
                  ></v-select>
                </b-form-group>
              </b-col>
              <b-col md="5" lg="5">
                <b-form-group label="" label-for="billing_date" class="label_input">
                  <v-select
                    class="main-vselect"
                    v-model="item.requestModel.paymentMethod"
                    :options="paymentMethodList"
                    :clearable="false"
                    disabled
                  ></v-select>
                </b-form-group>
              </b-col>
            </b-row>
            <b-row class="px-4 py-1 mt-3">
              <b-col md="5" lg="4" class="pl-0">
                <b-form-group label="" label-for="payment_scheduled_date" class="label_input">
                  <date-picker
                    v-model="item.requestModel.scheduledPaymentOn" 
                    value-type="YYYY-MM-DD"
                    format="YYYY/MM/DD"
                    disabled
                  ></date-picker>
                </b-form-group>
              </b-col>
              <b-col md="5" lg="5">
                <b-form-group label="" label-for="payment_date" class="label_input">
                  <date-picker
                    v-if="item.requestModel"
                    v-model="item.requestModel.paymentOn" 
                    value-type="YYYY-MM-DD"
                    format="YYYY/MM/DD"
                    disabled
                  ></date-picker>
                </b-form-group>
              </b-col>
            </b-row>
            <b-row class="px-4 py-1 mt-3">
              <b-col md="7" lg="7" class="pl-0">
                <b-form-group label="" label-for="payment_destination" class="label_input">
                  <b-form-input
                    v-if="item.requestModel"
                    type="text"
                    id="payment_destination"
                    v-model="item.requestModel.paymentDestination"
                    disabled></b-form-input>
                  </b-form-group>
              </b-col>
              <b-col md="5" lg="5">
                <b-form-group label="" label-for="account_number" class="label_input">
                  <b-form-input
                    v-if="item.requestModel"
                    type="text"
                    id="account_number"
                    v-model="item.requestModel.bankName"
                    disabled
                  ></b-form-input>
                </b-form-group>
              </b-col>
            </b-row>
            <b-row class="px-4 py-1 mt-3">
              <b-col md="8" class="pl-0">
                <table class="table_payment table_payment_1">
                  <thead>
                    <tr>
                      <th></th>
                      <th>8%()</th>
                      <th>10%()</th>
                      <th>()</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td></td>
                      <td>
                        <v-numeric
                          separator=","
                          class="form-control"
                          v-model="item.requestModel.itemTotalForEightPercent"
                          disabled
                        ></v-numeric>
                      </td>
                      <td>
                        <v-numeric
                          separator=","
                          class="form-control"
                          v-model="item.requestModel.itemTotalForTenPercent"
                          disabled
                        ></v-numeric>
                      </td>
                      <td>
                        <v-numeric
                          separator=","
                          class="form-control"
                          v-model="item.requestModel.itemTotal"
                          disabled
                        ></v-numeric>
                      </td>
                    </tr>
                    <tr>
                      <td></td>
                      <td>
                        <v-numeric
                          separator=","
                          class="form-control"
                          v-model="item.requestModel.consumptionTaxTotalForEightPercent"
                          disabled
                        ></v-numeric>
                      </td>
                      <td>
                        <v-numeric
                          separator=","
                          class="form-control"
                          v-model="item.requestModel.consumptionTaxTotalForTenPercent"
                          disabled
                        ></v-numeric>
                      </td>
                      <td>
                        <v-numeric
                          separator=","
                          class="form-control"
                          v-model="item.requestModel.consumptionTaxTotal"
                          disabled
                        ></v-numeric>
                      </td>
                    </tr>
                    <tr>
                      <td></td>
                      <td>
                        <v-numeric
                          separator=","
                          class="form-control"
                          v-model="item.requestModel.totalForEightPercent"
                          disabled
                        ></v-numeric>
                      </td>
                      <td>
                        <v-numeric
                          separator=","
                          class="form-control"
                          v-model="item.requestModel.totalForTenPercent"
                          disabled
                        ></v-numeric>
                      </td>
                      <td>
                        <v-numeric
                          separator=","
                          class="form-control"
                          v-model="item.requestModel.total"
                          disabled
                        ></v-numeric>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </b-col>
            </b-row>
          </template>
        </collapse>
        <!--  -->
        <collapse>
          <template v-slot:collapse-header></template>
          <template v-slot:collapse-body>
            <data-table
              v-if="!item.detailsForAccountsReceivablesRequestList || item.detailsForAccountsReceivablesRequestList.length > 0"
              key-list="detailsForAccountsReceivablesRequestList"
              :columns="salesUnpaidColumns"
              :data="item.detailsForAccountsReceivablesRequestList"
              :disabled="disabled"
              @addItem="addItem"
              @copyItem="copyItem"
              @removeItem="removeItem"
            ></data-table>
            <p v-else></p>
          </template>
        </collapse>
        <!-- 8% -->
        <collapse>
          <template v-slot:collapse-header>8%</template>
          <template v-slot:collapse-body>
            <data-table
              v-if="!item.detailsForPromotionalExpensesRequestTax8PercentList || item.detailsForPromotionalExpensesRequestTax8PercentList.length > 0"
              key-list="detailsForPromotionalExpensesRequestTax8PercentList"
              :columns="tax8PerctgSalesPromotionColumns"
              :data="item.detailsForPromotionalExpensesRequestTax8PercentList"
              :disabled="disabled"
              @addItem="addItem"
              @copyItem="copyItem"
              @removeItem="removeItem"
            ></data-table>
            <p v-else></p>
          </template>
        </collapse>
        <!-- 10% -->
        <collapse>
          <template v-slot:collapse-header>10%</template>
          <template v-slot:collapse-body>
            <data-table
              v-if="!item.detailsForPromotionalExpensesRequestTax10PercentList || item.detailsForPromotionalExpensesRequestTax10PercentList.length > 0"
              key-list="detailsForPromotionalExpensesRequestTax10PercentList"
              :columns="tax10PerctgSalesPromotionColumns"
              :data="item.detailsForPromotionalExpensesRequestTax10PercentList"
              :footer-data="sumAccruedAmount(item.detailsForPromotionalExpensesRequestTax10PercentList)"
              :disabled="disabled"
              @addItem="addItem"
              @copyItem="copyItem"
              @removeItem="removeItem"
            ></data-table>
            <p v-else></p>
          </template>
        </collapse>
        <!--  -->
        <collapse>
          <template v-slot:collapse-header></template>
          <template v-slot:collapse-body v-if="item.detailsForPromotionalExpensesRequestLogisticFeesList">
            <data-table
              v-if="!item.detailsForPromotionalExpensesRequestLogisticFeesList || item.detailsForPromotionalExpensesRequestLogisticFeesList.length > 0"
              key-list="detailsForPromotionalExpensesRequestLogisticFeesList"
              :columns="logicPromotionFeeColumns"
              :data="item.detailsForPromotionalExpensesRequestLogisticFeesList"
              :disabled="disabled"
              @addItem="addItem"
              @copyItem="copyItem"
              @removeItem="removeItem"
            ></data-table>
            <p v-else></p>
          </template>
        </collapse>
        <CommentSlideDialog
          v-if="isShowPopupComment && selectedItemNumber" 
          v-model="isShowPopupComment" 
          :initComment="initComment"
          :mainItemId="commentRecordCd"
          :mainItemNumber="selectedItemNumber.itemNumber" 
          @onAddedComment="onAddedComment"/>
      </b-container>
      <div class="request-footer btnForRequest">
        <template v-if="mode != 'edit'">
          <b-button class="btn-white" variant="none" @click="$router.push('/request/confirm')"> <br>
          </b-button>
          <b-button class="btn-white" variant="none" @click="$router.replace(`/account-receivables/confirm-create/${requestCd}/edit`)">
            
          </b-button>
          <b-button class="btn-blue" variant="none" @click="confirmRequest"><br/>
          </b-button>
        </template>
        <template v-else>
          <b-button class="btn-white" variant="none" @click="$router.replace(`/account-receivables/confirm-create/${requestCd}/view`)"> 
            
          </b-button>
          <b-button class="btn-white" variant="none" @click="saveRequest">
            
          </b-button>
        </template>
      </div>
    </section>
  </ValidationObserver>
</template>
<script>
import cloneDeep from "lodash/cloneDeep";
import { mapState, mapActions } from "vuex";
import AccountReceivableCreateService from '@/services/AccountReceivableCreateService';
import FormatNumeral from "@/mixins/FormatNumeral";
export default {
  name: "ConfirmCreate",
  mixins: [FormatNumeral],
  data() {
    return {
      item: {},
      isShowPopupComment: false,
      initComment: "",
      commentRecordCd: null,
      selectedItemNumber: null
    };
  },
  computed:{
    ...mapState({
      loginUser: state => state.AuthenticationModule.loginUser,
      useTypesList: state => state.AccountReceivableCreateModule["useTypesList"],
      commissionTypeList: state => state.AccountReceivableCreateModule["commissionTypeList"],
      departmentList: state => state.AccountReceivableCreateModule["departmentList"],
      applicationList: state => state.AccountReceivableCreateModule["applicationList"],
      paymentMethodList: state => state.AccountReceivableCreateModule["paymentMethodList"],
      paymentPlaceList: state => state.AccountReceivableCreateModule["paymentPlaceList"],
      listChargeName: state => state.AccountReceivableCreateModule["listChargeName"],
      listItem: state => state.AccountReceivableCreateModule["listItem"],
      listClassification: state => state.AccountReceivableCreateModule["listClassification"]
    }),
    requestCd() {
      return this.$route.params.cd;
    },
    requestStatus() {
       let status = this.item.requestModel.mstRequestStatusesResponse.name;
       if(this.$route.params.mode == "edit") {
        status += "<br/>";
       }
       return status;
    },
    mode() {
      return this.$route.params.mode;
    },
    disabled() {
      return this.$route.params.mode != "edit";
    },
    salesUnpaidColumns() {
      return [
        this.getColumnByKey("", "itemNumber"),
        this.getCheckDiffPriceColumn(),
        this.getConfirmColumn(),
        this.getCommentColumn(),
        this.getInputColumnByKey("", "storeGNm", "text", true, { required: true }, {
            required: '',
            max: ''
          }, 1, 40, false, function(datum, event) {}
        ),
        this.getSelectColumnByKey("", "shainCd", [], true, { required: true }, {
            required: ''
          }, true
        ),
        this.getSelectColumnByKey( "", "bumonCd", [], true, { required: true },
          {
            required: ""
          }, true
        ),
        this.getSelectColumnByKey("", "typeOfItem", this.listItem, true, { required: true }, {
            required: ""
          }, false
        ),
        this.getSelectColumnByKey("", "hinmokuCd", [], true,{ required: true }, {
            required: ""
          }, true
        ),
        this.getInputColumnByKey("", "hinmokuRnm", "text", true, { required: true }, {
            required: "",
            max: ""
          }, 1, 30, true
        ),
        this.getInputColumnByKey("", "yoryo", "text", true, { required: true },
          {
            required: "",
            max: ""
          }, 1, 8, false
        ),
        this.getNumericColumnByKey("", "irisu", true, { required: true },
          {
            required: ""
          }, 0, 9999, 1, "initialIrisu"
        ),
        this.getNumericColumnByKey("()", "mfYen", true, { required: true },
          {
            required: ""
          }, 0.00, 99999999.99, 0.01, "initialMfYen"
        ),
        this.getNumericColumnByKey("(%)", "commission", true, { required: true },
          {
            required: ""
          }, 0.00, 999999999999.99, 0.01, "initialCommission"
        ),
        this.getNumericColumnByKey("c/s()", "csdiscount", true, { required: true },
          {
            required: "c/s"
          }, 0.00, 999999999999.99, 0.01, "initialCSDiscount"
        ),
        this.getNumericColumnByKey("NET()", "denpyoNet", true, { required: true },
          {
            required: "NET"
          }, 0.00, 999999999999.99, 0.01, "initialDenpyoNet"
        ),
        this.getNumericColumnByKey("()", "accruedAmount", true, { required: true },
          {
            required: ""
          }, 0.00, 999999999999.99, 0.01, "initialAccruedAmount"
        ),
        this.getNumericColumnByKey("()", "finalTakeUnitPrice", true, { required: true },
          {
            required: ""
          }, 0.00, 999999999999.99, 0.01, "initialFinalTakeUnitPrice"
        ),
        this.getNumericColumnByKey("()()", "hyojyunYen", true, { required: true },
          {
            required: "()"
          }, 0.00, 99999999.99, 0.01, "initialHyojyunYen"
        ),
        this.getNumericColumnByKey("", "quantityOfSoldItems", true, { required: true },
          {
            required: ""
          }, 0, 999999999999, 1, "initialQuantityOfSoldItems"
        ),
        this.getNumericColumnByKey("()", "accruedAmount", true, { required: true },
          {
            required: ""
          }, 0, 999999999999, 1, "initialAccruedAmount"
        ),
        this.getNumericColumnByKey("()", "salesAmount", true, { required: true },
          {
            required: ""
          }, 0, 999999999999, 1, "initialSalesAmount"
        ),
        this.getNumericColumnByKey("()", "finalMarginalProfit", true, { required: true },
          {
            required: ""
          }, 0, 99999999999, 1, "initialFinalMarginalProfit"
        ),
        this.getNumericColumnByKey("(%)", "finalMarginalProfitRatio", true, { required: true },
          {
            required: ""
          }, 0.00, 999999999999.99, 0.01, "initialFinalMarginalProfitRatio"
        )
      ];
    },
    tax8PerctgSalesPromotionColumns() {
      return [
        this.getColumnByKey("", "itemNumber", null),
        this.getCheckDiffPriceColumn(),
        this.getConfirmColumn(),
        this.getCommentColumn(),
        this.getInputColumnByKey("", "storeGNm", "text", true, { required: true }, {
            required: '',
            max: ''
          }, 1, 40, false, function(datum, event) {}
        ),
        this.getSelectColumnByKey("", "shainCd", [], true, { required: true }, {
            required: ''
          }, true
        ),
        this.getSelectColumnByKey( "", "bumonCd", [], true, { required: true },
          {
            required: ""
          }, true
        ),
        this.getSelectColumnByKey("", "typeOfPromotionalExpenses", this.listItem, true, { required: true }, {
            required: ""
          }, false
        ),
        this.getSelectColumnByKey("", "typeOfInput", [], true, { required: true }, {
            required: ""
          }, true
        ),
        this.getSelectColumnByKey("", "classification", this.listClassification, true, { required: true }, {
            required: ""
          }, true
        ),
        this.getSelectColumnByKey("	", "brandClassification", this.listClassification, true, { required: true }, {
            required: ""
          }, true
        ),
        this.getSelectColumnByKey("", "categoryName", [], true,{ required: true }, {
            required: ""
          }, true
        ),
        this.getSelectColumnByKey("", "hinmokuCd", [], true,{ required: true }, {
            required: ""
          }, true
        ),
        this.getInputColumnByKey("", "hinmokuRnm", "text", true, { required: true }, {
            required: "",
            max: ""
          }, 1, 30, true
        ),
        this.getInputColumnByKey("", "nisugata", "text", true, { required: true }, {
            required: "",
            max: ""
          }, 1, 15, false
        ),
        this.getNumericColumnByKey("()", "accruedAmount", true, { required: true },
          {
            required: ""
          }, 0, 999999999999, 1, "initialAccruedAmount"
        )
      ];
    },
    tax10PerctgSalesPromotionColumns() {
      return [
        this.getColumnByKey("", "itemNumber", null),
        this.getCheckDiffPriceColumn(),
        this.getConfirmColumn(),
        this.getCommentColumn(),
        this.getInputColumnByKey("", "storeGNm", "text", true, { required: true }, {
            required: '',
            max: ''
          }, 1, 40, false, function(datum, event) {}
        ),
        this.getSelectColumnByKey("", "shainCd", [], true, { required: true }, {
            required: ''
          }, true
        ),
        this.getSelectColumnByKey( "", "bumonCd", [], true, { required: true },
          {
            required: ""
          }, true
        ),
        this.getSelectColumnByKey("", "typeOfPromotionalExpenses", this.listItem, true, { required: true }, {
            required: ""
          }, false
        ),
        this.getSelectColumnByKey("", "typeOfInput", [], true, { required: true }, {
            required: ""
          }, true
        ),
        this.getSelectColumnByKey("", "classification", this.listClassification, true, { required: true }, {
            required: ""
          }, true
        ),
        this.getSelectColumnByKey("	", "brandClassification", this.listClassification, true, { required: true }, {
            required: ""
          }, true
        ),
        this.getSelectColumnByKey("", "categoryName", [], true,{ required: true }, {
            required: ""
          }, true
        ),
        this.getSelectColumnByKey("", "hinmokuCd", [], true,{ required: true }, {
            required: ""
          }, true
        ),
        this.getInputColumnByKey("", "hinmokuRnm", "text", true, { required: true }, {
            required: "",
            max: ""
          }, 1, 30, true
        ),
        this.getInputColumnByKey("", "nisugata", "text", true, { required: true }, {
            required: "",
            max: ""
          }, 1, 15, false
        ),
        this.getNumericColumnByKey("()", "accruedAmount", true, { required: true },
          {
            required: ""
          }, 0, 999999999999, 1, "initialAccruedAmount"
        )
      ];
    },
    logicPromotionFeeColumns() {
      return [
        this.getColumnByKey("", "itemNumber", null),
        this.getCheckDiffPriceColumn(),
        this.getConfirmColumn(),
        this.getCommentColumn(),
        this.getInputColumnByKey("", "storeGNm", "text", true, { required: true }, {
            required: '',
            max: ''
          }, 1, 40, false, function(datum, event) {}
        ),
        this.getSelectColumnByKey("", "shainCd", [], true, { required: true }, {
            required: ''
          }, true
        ),
        this.getSelectColumnByKey( "", "bumonCd", [], true, { required: true },
          {
            required: ""
          }, true
        ),
        this.getSelectColumnByKey("", "typeOfPromotionalExpenses", this.listItem, true, { required: true }, {
            required: ""
          }, false
        ),
        this.getSelectColumnByKey("", "typeOfInput", [], true, { required: true }, {
            required: ""
          }, true
        ),
        this.getSelectColumnByKey("", "classification", this.listClassification, true, { required: true }, {
            required: ""
          }, true
        ),
        this.getSelectColumnByKey("	", "brandClassification", this.listClassification, true, { required: true }, {
            required: ""
          }, true
        ),
        this.getSelectColumnByKey("", "categoryName", [], true,{ required: true }, {
            required: ""
          }, true
        ),
        this.getSelectColumnByKey("", "hinmokuCd", [], true,{ required: true }, {
            required: ""
          }, true
        ),
        this.getInputColumnByKey("", "hinmokuRnm", "text", true, { required: true }, {
            required: "",
            max: ""
          }, 1, 30, true
        ),
        this.getInputColumnByKey("", "nisugata", "text", true, { required: true }, {
            required: "",
            max: ""
          }, 1, 15, false
        ),
        this.getNumericColumnByKey("()", "accruedAmount", true, { required: true },
          {
            required: ""
          }, 0, 999999999999, 1, "initialAccruedAmount"
        )
      ];
    }
  },
  created() {
    if(!this.requestCd) {
      this.$router.push("/request/confirm");
    } else {
      if(!this.item.requestModel) {
        this.item.requestModel = {};
      }
      if(!this.item.requestModel.mstRequestStatusesResponse) {
        this.item.requestModel.mstRequestStatusesResponse = {};
      }
      this.setModeAccountReceivableCreate('confirm-create');
      this.getUseTypes();
      this.getCommissionTypes();
      this.getListDepartment();
      this.getListPaymentPlace();
      this.getListPaymentMethod();
      this.getListItem();
      this.fetchRequest();
    }
  },
  methods:{
    ...mapActions('AccountReceivableCreateModule', ["getUseTypes", "getCommissionTypes", 'getListDepartment', 'getListApplication', 'getListPaymentPlace',
        'getListPaymentMethod', 'createAccountReceivables', "deleteAttachFile", "addItemListManageItem", "addPromotions8Percents",
        "addPromotions10Percents", "addPromotionsCostLogistics", "getListItem", "setAttachFileForRequest",
        "deleteItemAccountReceivablesDetailRequestList", "addItemComment", "addItemAccountReceivablesProduct", "setValueDenpyoNet", 'final_take_unit_price', 'accrued_amount', 'sales_amount', 'final_marginal_profit', 'final_marginal_profit_ratio'
        , "getListPromotionExpenses", "getListInputType", "getListClassifications", "getListBrandClassification", "getListCategory", "getListChargeName", "setChargeNameForRequestTable", "setDepartmentNameForRequestTable"
        , "getDetailAccountReceivable", "setModeAccountReceivableCreate"
      ]),
    fetchRequest() {
      AccountReceivableCreateService.getDetailAccountReceivableByRequestId({cd: this.requestCd, mode: "view"})
        .then(response => {
          if(response.data) {
            this.item = response.data;
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    getColumnByKey(label, field, getData) {
      return {
        label: label,
        field: field,
        getData: getData
      }
    },
    getInputColumnByKey(label, field, type, isValidate, validateRule, messages, min, max, isReadOnly, onInput) {
      return {
        label: label,
        field: field,
        isInput: true,
        type: type ? type : "text",
        isValidate: isValidate,
        validateRule: validateRule,
        messages: messages,
        isDisable: this.disabled,
        isPlainText: true, // read only
        min: min,
        max: max,
        isReadOnly: isReadOnly,
        onInput: onInput
      }
    },
    getNumericColumnByKey(label, field, isValidate, validateRule, messages, min, max, step, initialField) {
      return {
        label: label,
        field: field,
        isInput: true,
        type: "numeric",
        isValidate: isValidate,
        validateRule: validateRule,
        messages: messages,
        isDisable: this.disabled,
        min: min,
        max: max,
        step: step ? step : 0.01,
        initialField: initialField,
        onInput: (datum, event) => {
        }
      }
    },
    getSelectColumnByKey(label, field, options, isValidate, validateRule, messages, isOnSearch = false) {
      return {
        label: label,
        field: field,
        isSelect: true,
        isDisable: this.disabled,
        isValidate: isValidate,
        validateRule: validateRule,
        messages: messages,
        options: options,
        onInput: (data, event) => {
        },
        onOpen: () => {
        },
        isOnSearch: isOnSearch,
        onSearch: (event) => {
          if (event && event.target.value) {
            let value = event.target.value;
            this.getListChargeName(value).then(res => {
              console.log(res)
              let op = this.listChargeName.reduce((list, item) => {
                let option = [{text: item.shainNm, value: item}]
                return [...list, ...option]
              }, []);
            }).catch(err => {
              console.log(err);
            })
          }
        }
      }
    },
    getCommentColumn() {
      return {
        label: "",
        isComment: true,
        status: data => {
          return data.hasComment || (data.commentDetailsRequests && data.commentDetailsRequests.length > 0); 
        },
        onClick: (data, index, listName) => {
          this.selectedItemNumber = {rowIndex: index, listName: listName, itemNumber: data.itemNumber};
          this.isShowPopupComment = true;
          if(this.item[listName][index].commentDetailsRequests){
            this.initComment = this.item[listName][index].commentDetailsRequests[0].comment;
          }
          this.commentRecordCd = this.item[listName][index]?.accountReceivableDetailCd;
        }
      }
    },
    getCheckDiffPriceColumn() {
      return {
        label: "",
        isCheckDiffPrice: true,
        status: (data, columns) => {
          let isDiff = false;
          for(let i = 0; i < columns.length; i ++) {
            let col = columns[i];
            if(col.type == "numeric" && data[col.field] != data[col.initialField]) {
              isDiff = true;
              break;
            }
          }
          return isDiff; 
        }
      }
    },
    getConfirmColumn() {
      return {
        label: "",
        isConfirm: true,
        status: data => {
          return data.isChecked ? "confirmed" : data.shainCd == this.loginUser.sub ? "not-confirm" : ""
        },
        onClick: (data, listName) => {
          if(data.shainCd == this.loginUser.sub) {
            data.isChecked = data.isChecked == 1 ? 0 : 1;
            let list = this.item[listName] || [];
            let indexItem = list.findIndex(item => {
              return item.cd == data.cd;
            });
            this.$set(list, indexItem, data);
            this.$set(this.item, listName, list);
          }
        }
      }
    },
    formatterDate(value, formatStr) {
      this.$moment(value).format(formatStr);
    },
    addItem() {},
    copyItem() {},
    removeItem () {},
    onAddedComment(newComment) {
      let payload = {
        selectedItem: {
          index: this.selectedItemNumber.rowIndex,
          listName: this.selectedItemNumber.listName
        }, comment: newComment
      }
      let storeName = this.item[payload.selectedItem.listName][payload.selectedItem.index].storeGNm;
      let chargeName = this.item[payload.selectedItem.listName][payload.selectedItem.index].shainNm;
      if (payload.comment.isSameStoreName || payload.comment.isSameShain || payload.comment.isSameShainExclamation) {
        this.item[payload.selectedItem.listName].forEach((item, index) => {
          if (item.storeGNm == storeName && payload.comment.isSameStoreName && this.item[payload.selectedItem.listName][payload.selectedItem.index].commentDetailsRequests) {
            this.item[payload.selectedItem.listName][index].commentDetailsRequests[0].comment = payload.comment.comment
          } else {
            if (item.shainNm == chargeName && payload.comment.isSameShain && this.item[payload.selectedItem.listName][payload.selectedItem.index].commentDetailsRequests) {
              this.item[payload.selectedItem.listName][payload.selectedItem.index].commentDetailsRequests[0].comment = payload.comment.comment;
            } else {
              this.item[payload.selectedItem.listName][index].commentDetailsRequests = [{
                comment: payload.comment.comment,
                isDeputy: 0
              }];
            }
          }

        })
      } else {
        if (this.item[payload.selectedItem.listName][payload.selectedItem.index].commentDetailsRequests) {
          this.item[payload.selectedItem.listName][payload.selectedItem.index].commentDetailsRequests[0].comment = payload.comment.comment
        } else {
          this.item[payload.selectedItem.listName][payload.selectedItem.index].commentDetailsRequests = [{
            comment: payload.comment.comment,
            isDeputy: 0
          }];
        }
      }
      this.item[payload.selectedItem.listName][payload.selectedItem.index].hasComment = this.item[payload.selectedItem.listName][payload.selectedItem.index]?.commentDetailsRequests.length > 0;
    },
    sumAccruedAmount(list) {
      if(!list || !Array.isArray(list)) {
        return 0;
      }
      return list.reduce(( a,b ) => {
        let accruedAmountA = a.accruedAmount || 0,
          accruedAmountB = b.accruedAmount || 0;
        return accruedAmountA + accruedAmountB
      }, 0);
    },
    confirmRequest() {
      // check data validate
      // get all row which are belong to login user
      let columnsToCheck = [];
      let setColumnFields = new Set();
      columnsToCheck = columnsToCheck.concat(this.salesUnpaidColumns.filter(column => {
        if(!setColumnFields.has(column.initialField) && column.initialField) {
          setColumnFields.add(column.initialField);
          return column;
        }
      }));
      columnsToCheck = columnsToCheck.concat(this.tax8PerctgSalesPromotionColumns.filter(column => {
        if(!setColumnFields.has(column.initialField) && column.initialField) {
          setColumnFields.add(column.initialField);
          return column;
        }
      }));
      columnsToCheck = columnsToCheck.concat(this.tax10PerctgSalesPromotionColumns.filter(column => {
        if(!setColumnFields.has(column.initialField) && column.initialField) {
          setColumnFields.add(column.initialField);
          return column;
        }
      }));
      columnsToCheck = columnsToCheck.concat(this.logicPromotionFeeColumns.filter(column => {
        if(!setColumnFields.has(column.initialField) && column.initialField) {
          setColumnFields.add(column.initialField);
          return column;
        }
      }));
      let listRowsNeedChecked = [],
        listRowNeedCheckDiffHasComment = [];
      if(this.item.detailsForAccountsReceivablesRequestList) {
        listRowsNeedChecked= listRowsNeedChecked.concat(this.item.detailsForAccountsReceivablesRequestList.filter(data => {
          return data.shainCd == this.loginUser.sub;
        }));
      }
      if(this.item.detailsForPromotionalExpensesRequestTax8PercentList) {
        listRowsNeedChecked= listRowsNeedChecked.concat(this.item.detailsForPromotionalExpensesRequestTax8PercentList.filter(data => {
          return data.shainCd == this.loginUser.sub;
        }));
      }
      if(this.item.detailsForPromotionalExpensesRequestTax10PercentList) {
        listRowsNeedChecked= listRowsNeedChecked.concat(this.item.detailsForPromotionalExpensesRequestTax10PercentList.filter(data => {
          return data.shainCd == this.loginUser.sub;
        }));
      }
      if(this.item.detailsForPromotionalExpensesRequestLogisticFeesList) {
        listRowsNeedChecked= listRowsNeedChecked.concat(this.item.detailsForPromotionalExpensesRequestLogisticFeesList.filter(data => {
          return data.shainCd == this.loginUser.sub;
        }));
      }
      let isCheckedAllRows = listRowsNeedChecked.every(data => data.isChecked);

      listRowNeedCheckDiffHasComment = listRowsNeedChecked.filter(data => {
        let isChangeInitital = columnsToCheck.some(column => {
          return data[column.field] != data[column.initialField];
        });
        return isChangeInitital && !data.hasComment;
      });
      console.log(listRowNeedCheckDiffHasComment)
      let isFillCommentForAllChangeInitialValue = listRowNeedCheckDiffHasComment.length > 0;
      console.log(isCheckedAllRows,isFillCommentForAllChangeInitialValue)
      if(!isCheckedAllRows || isFillCommentForAllChangeInitialValue) {
        if(listRowNeedCheckDiffHasComment.length > 0) {
          // set error to comment
          let listNames = [
            "detailsForAccountsReceivablesRequestList",
            "detailsForPromotionalExpensesRequestTax8PercentList",
            "detailsForPromotionalExpensesRequestTax10PercentList",
            "detailsForPromotionalExpensesRequestLogisticFeesList"
          ];
          listRowNeedCheckDiffHasComment.forEach(data => {
            // find data in list
            let dataIndex = -1;
            for(let i = 0; i< listNames.length; i++) {
              let listName = listNames[i];
              let list = this.item[listName];
              dataIndex = list.findIndex(item => {
                return item.cd == data.cd;
              });
              if(dataIndex > -1) {
                data.hasDiff = true;
                this.$set(list, dataIndex, data);
                this.$set(this.item, listName, list);
                break;
              }
            }
          })
        }
        
      } else {
        AccountReceivableCreateService.saveAccountReceivableConfirmRequest(null, this.item)
        .then(response => {
          this.fetchRequest();
        })
        .catch(err => {
          console.log(err)
        })
      }
      // Save request
    },
    saveRequest() {
      console.log(this.item);
    },
    checkGoComment(row, col) {
      if (this.list[row][col].hascomment) {
        this.list[row][col].hascomment = 0;
        this.list[row][col + 1].hascomment = 2;
        this.list[row][col + 1].disabled = true;
      } else {
        this.list[row][col].hascomment = 1;
        this.list[row][col + 1].hascomment = 0;
        this.list[row][col + 1].disabled = false;
      }
      console.log(row,col)
    },
    // addItem() {
    //   this.list.push(cloneDeep(this.items));
    // },
    removeITem() {
      if (this.list.length > 1) {
        this.list.pop();
      }
    },
    showPopupComnent (rowIndex, columnIndex) {
      console.log(rowIndex);
      this.isShowPopupComment = !this.isShowPopupComment;
    },
    searchProduct() {
      alert("search product")
    },
    getValueTable(){
      var valueList = [];
      this.list.forEach(item => {
        var t = item.reduce((value, elm) => {
          if (elm["value"] !== undefined) {
            if(elm['value'] == ""){
              return [...value,..." "];
            }else{
              return [...value,...elm["value"]];
            }
          } else {
            return [...value];
          }
        }, []);
        valueList.push(t);
      });
    }
  }
};
</script>

